<template lang="html">
  <div class="answerList">
    <ul>
      <template v-if="answerList.length !==0">
        <li v-for="(data,index) in answerList">
          <div class="oneAnswer">
            <div class="personInfo clearFix">
              <img :src="data.headImg" alt="头像">
              <div class="Info">
                <p>{{data.userName}}</p>
                <p>{{data.introduction}}</p>
              </div>
            </div>
            <p>{{data.good}}人赞同了该回答</p>
            <div class="content" v-html='data.answerContent'>
            </div>
            <p>编辑于 {{data.updateTime}}</p>
            <div class="actionBar">
              <p>
                <a href="javascript:;"  @click="action">{{data.good}}赞同</a>
                <a href="javascript:;" @click="action">踩</a>
                <a href="javascript:;">{{(data.disscusCode === null)?0:1}}条评论</a>
                <a href="javascript:;">分享</a>
                <a href="javascript:;">收藏</a>
                <a href="感谢"></a>
              </p>

            </div>
          </div>
        </li>
      </template>
      <template v-else>
        <li>暂无回答哦</li>
      </template>

    </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      answerList:[]
    }
  },
  methods:{
    getAnswerList () {
      let _this = this;
      this.$http.get('/api/getAnswerList?code='+ parseInt(_this.$route.params.id) + '&r=' + Math.random()).then(data=>{
        if(data.body.success===true){
          this.answerList = data.body.data;
        }else{
          alert(data.body.message);
        }
      })
    },
    action () {
      
    }
  },
  mounted () {
    this.getAnswerList();
  }
}
</script>

<style lang="less">
  .answerList{
    margin-top:10px;
    ul{
      li{
        background:#fff;
        margin-bottom:10px;
        .oneAnswer{
          padding:15px 25px;
          .personInfo{
            img{
              float:left;
              width:38px;
              height:38px;
              margin-right:15px;
            }
            .Info{
              float:left;
              p{
                line-height:1.3;
              }
              p:first-child{
                font-weight: 600;
              }
              p:last-child{
                color:#555;
              }
            }
          }
          >p{
            color:#8590a6;
            margin:10px 0;
          }
        }
      }
    }
  }
</style>



