<template lang="html">
  <div class="questionDetail">
    <div class="minBody clearFix">
      <div class="leftPart">
        <p class="topicList"><span v-for="data in topic">{{data.topicName}}</span></p>
        <h1>{{questInfo.questName}}</h1>
        <div class="questionContent">
          {{questInfo.describetion}}
        </div>
        <div class="actionBar">
          <p>
            <ul>
              <li>
                <a href="#" class="focusQuest">关注问题</a>
              </li>
              <li>
                <a href="javascript:;" class="takeAnswer" @click="showWriteAnsBox">写回答</a>
              </li>
              <li>
                <a href="javascript:;" @click="showDisscusFn" class="disscusNum">{{showDisscus.dissCount}}条评论</a>
              </li>
              <li>
                <a href="#" class="share">分享</a>
              </li>
              <li>
                <a href="#" class="invite">邀请回答</a>
              </li>
              <li>
                <a href="#" class="report">举报</a>
              </li>
              <li>
                <a href="#" class="soOn">···</a>
              </li>
              <li>
                <a href="#" class="takeClose">收起</a>
              </li>
            </ul>
          </p>
        </div>
      </div>
      <div class="numInfo">
        <div class="clearFix">
          <div class="follower">
            <p>关注者</p>
            <p>{{questInfo.follower}}</p>
          </div>
          <div class="watched">
            <p>被浏览</p>
            <p>{{questInfo.watched}}</p>
          </div>
        </div>
        <div class="followerList">
          <p>lydia也关注了该问题</p>
        </div>
      </div>
    </div>
    <disscusBox @childData="changeStatus" :showStatus="showDisscus"></disscusBox>
  </div>
</template>

<script>
import disscusBox from '@/components/disscus'
export default {
  data() {
    return {
      questInfo: {},
      topic: [],
      showDisscus: {
        showStatus : false,
        dissCount:0,
        codeNum: this.$route.params.id
      }
    }
  },
  components:{
    disscusBox
  },
  methods: {
    //获取问题详情
    getQuestDetial (code) {
      this.$http.get('/api/getQuestDetail?code=' + code + '&r=' + Math.random()).then(data => {
        if (data.body.success === true) {
          this.topic = data.body.data.topic;
          this.questInfo = data.body.data.questInfo;
          this.showDisscus.dissCount = data.body.data.disscusCount;
          this.$emit("title",this.questInfo.questName);
        }
      })
    },
    //显示评论框
    showDisscusFn () {
      this.showDisscus.showStatus = true;
    },
    //隐藏评论框
    changeStatus(childData){
      this.showDisscus.showStatus = childData.status;
      this.showDisscus.dissCount = childData.dissCount;
    },
    //判断是否登陆
    loginStatusFn (callBack) {
      this.$http.get("/api/loginStatus?r=" + Math.random()).then(data=>{
        if (data.body.success === true) {
          callBack && callBack();
        }else{
          alert(data.body.message);
        }
      })
    },
    //显示写答案的框
    showWriteAnsBox () {
      let _this = this
      this.loginStatusFn(function(){
        _this.$emit("showAnsBox",true);
      })

    }
  },
  mounted () {
    this.getQuestDetial(this.showDisscus.codeNum);
  },
  watch:{
    '$route' (val,oldVal) {
      this.showDisscus.codeNum = val.params.id;
      this.getQuestDetial(val.params.id);
    }
  }

}
</script>

<style lang="less">
.questionDetail {
    position: relative;
    width: 100%;
    background-color: #fff;
    margin-top: 56px;
    .minBody {
        width: 1000px;
        margin: 0 auto;
        background-color: #fff;
        padding: 20px 10px;
        .leftPart {
            width: 694px;
            float: left;
            ul {
                li {
                    float: left;
                    margin-right: 20px;
                    a {
                        padding: 5px 10px;
                        border-radius: 5px;
                    }
                    .focusQuest {
                        color: #fff;
                        background-color: #0f88eb;
                        border: 1xp solid #0f88eb;
                    }
                    .takeAnswer {
                        color: #0f88eb;
                        background-color: #fff;
                        border: 1px solid #0f88eb;
                    }
                }
            }
            h1 {
                font-size: 22px;
                margin: 12px 0 4px;
            }
            .topicList {
                span {
                    background-color: #eef4fa;
                    color: #3e7ac2;
                    font-size: 14px;
                    padding: 5px 10px;
                    border-radius: 15px;
                    margin-left: 10px;
                }
            }
            .questionContent {
                margin-bottom: 20px;
            }
        }
        .numInfo {
            float: right;
            .follower,
            .watched {
                padding: 10px;
                text-align: center;
                p {
                    &:first-child {
                        font-size: 14px;
                    }
                    &:last-child {
                        font-size: 18px;
                    }
                }
            }
            .follower {
                float: left;
                border-right: 1px solid #f0f2f7;
            }
            .watched {
                float: left;
            }
        }
    }
}
</style>



