//问题详情页头部，含滚动事件
<template lang="html">
  <div class="topNav">
    <transition name="fadeIn" mode="out-in">
      <div class="minWidth clearFix" v-if="show" key="userInfo">
        <div class="logoBox">
          <svg viewBox="0 0 200 91" class="Icon Icon--logo" width="120" height="53" aria-hidden="true" style="fill: rgb(15, 136, 235); height: 33px; width: 70px;">
            <title></title>
            <g>
              <path d="M53.29 80.035l7.32.002 2.41 8.24 13.128-8.24h15.477v-67.98H53.29v67.978zm7.79-60.598h22.756v53.22h-8.73l-8.718 5.473-1.587-5.46-3.72-.012v-53.22zM46.818 43.162h-16.35c.545-8.467.687-16.12.687-22.955h15.987s.615-7.05-2.68-6.97H16.807c1.09-4.1 2.46-8.332 4.1-12.708 0 0-7.523 0-10.085 6.74-1.06 2.78-4.128 13.48-9.592 24.41 1.84-.2 7.927-.37 11.512-6.94.66-1.84.785-2.08 1.605-4.54h9.02c0 3.28-.374 20.9-.526 22.95H6.51c-3.67 0-4.863 7.38-4.863 7.38H22.14C20.765 66.11 13.385 79.24 0 89.62c6.403 1.828 12.784-.29 15.937-3.094 0 0 7.182-6.53 11.12-21.64L43.92 85.18s2.473-8.402-.388-12.496c-2.37-2.788-8.768-10.33-11.496-13.064l-4.57 3.627c1.363-4.368 2.183-8.61 2.46-12.71H49.19s-.027-7.38-2.372-7.38zm128.752-.502c6.51-8.013 14.054-18.302 14.054-18.302s-5.827-4.625-8.556-1.27c-1.874 2.548-11.51 15.063-11.51 15.063l6.012 4.51zm-46.903-18.462c-2.814-2.577-8.096.667-8.096.667s12.35 17.2 12.85 17.953l6.08-4.29s-8.02-11.752-10.83-14.33zM199.99 46.5c-6.18 0-40.908.292-40.953.292v-31.56c1.503 0 3.882-.124 7.14-.376 12.773-.753 21.914-1.25 27.427-1.504 0 0 3.817-8.496-.185-10.45-.96-.37-7.24 1.43-7.24 1.43s-51.63 5.153-72.61 5.64c.5 2.756 2.38 5.336 4.93 6.11 4.16 1.087 7.09.53 15.36.277 7.76-.5 13.65-.76 17.66-.76v31.19h-41.71s.88 6.97 7.97 7.14h33.73v22.16c0 4.364-3.498 6.87-7.65 6.6-4.4.034-8.15-.36-13.027-.566.623 1.24 1.977 4.496 6.035 6.824 3.087 1.502 5.054 2.053 8.13 2.053 9.237 0 14.27-5.4 14.027-14.16V53.93h38.235c3.026 0 2.72-7.432 2.72-7.432z" fill-rule="evenodd">
              </path>
            </g>
          </svg>
        </div>
        <div class="modelList">
          <ul>
            <li><a href="/" class="active">首页</a></li>
            <li><a href="/" class="">发现</a></li>
            <li><a href="/" class="">话题</a></li>
          </ul>
        </div>
        <div class="searchBox clearFix">
          <input type="text" name="" value="" placeholder="搜索你感兴趣的内容...">
          <a href="javascript:;">
            <svg viewBox="0 0 16 16" class="Icon Icon--search" width="16" height="16" aria-hidden="true" style="height: 16px; width: 16px; fill: #afbdcf;"><title></title><g><path d="M12.054 10.864c.887-1.14 1.42-2.57 1.42-4.127C13.474 3.017 10.457 0 6.737 0S0 3.016 0 6.737c0 3.72 3.016 6.737 6.737 6.737 1.556 0 2.985-.533 4.127-1.42l3.103 3.104c.765.46 1.705-.37 1.19-1.19l-3.103-3.104zm-5.317.925c-2.786 0-5.053-2.267-5.053-5.053S3.95 1.684 6.737 1.684 11.79 3.95 11.79 6.737 9.522 11.79 6.736 11.79z"></path></g></svg>
          </a>
        </div>
        <div class="askQuest">
          <a href="javascript:;" @click="login">提问</a>
        </div>
        <div class="personInfo">
          <div  v-if="loginStatus">
            <ul class="loginAfter clearFix" >
              <li>
                <a href="javascript:;" @click="showList($event,3)">
                  <svg viewBox="0 0 20 22" class="Icon Icon--news" width="20" height="20" aria-hidden="true" style="height: 20px; width: 20px;"><title></title><g><path d="M2.502 14.08C3.1 10.64 2 3 8.202 1.62 8.307.697 9.08 0 10 0s1.694.697 1.797 1.62C18 3 16.903 10.64 17.497 14.076c.106 1.102.736 1.855 1.7 2.108.527.142.868.66.793 1.206-.075.546-.542.95-1.09.943H1.1C.55 18.34.084 17.936.01 17.39c-.075-.547.266-1.064.794-1.206.963-.253 1.698-1.137 1.698-2.104zM10 22c-1.417.003-2.602-1.086-2.73-2.51-.004-.062.02-.124.063-.17.043-.045.104-.07.166-.07h5c.063 0 .124.025.167.07.044.046.067.108.063.17-.128 1.424-1.313 2.513-2.73 2.51z"></path></g></svg>
                </a>
              </li>
              <li>
                <a href="javascript:;" @click="showList($event,2)">
                  <svg viewBox="0 0 20 20" class="Icon Icon--message" width="20" height="20" aria-hidden="true" style="height: 20px; width: 20px;"><title></title><g><path d="M9 0C3.394 0 0 4.13 0 8c0 1.654.522 3.763 2.014 5.566.314.292.518.82.454 1.17-.165 1.488-.842 1.905-.842 1.905-.328.332.105.67.588.582 1.112-.2 2.07-.58 3.526-1.122.4-.202.464-.147.78-.078C11.524 17.764 18 14 18 8c0-3.665-3.43-8-9-8z"></path><path d="M19.14 9.628c.758.988.86 2.01.86 3.15 0 1.195-.62 3.11-1.368 3.938-.21.23-.354.467-.308.722.12 1.073.614 1.5.614 1.5.237.24-.188.563-.537.5-.802-.145-1.494-.42-2.545-.81-.29-.146-.336-.106-.563-.057-2.043.712-4.398.476-6.083-.926 5.964-.524 8.726-3.03 9.93-8.016z"></path></g></svg>
                </a>
              </li>
              <li>
                <a href="javascript:;" @click="showList($event,1)" class="headBtn"><img :src="userInfo.headImg" alt="头像"></a>
              </li>
            </ul>
            <div v-if="topheaderStatus.userShowList" class="topHeaderPop people">
              <ul>
                <li>
                  <a href="#">个人主页</a>
                </li>
                <li>
                  <a href="#">设置</a>
                </li>
                <li>
                  <a href="#" @click="logOut">退出</a>
                </li>
              </ul>
            </div>
            <div v-if="topheaderStatus.messageShowList" class="topHeaderPop message">
              <div class="title">我的私信</div>
              <ul>
                <li>
                  <div class="message_one clearFix">
                    <div class="left">
                      <img :src="userInfo.headImg" alt="">
                    </div>
                    <div class="right">
                      <p>知乎实验室</p>
                      <p>亲爱的胡钧赫你好</p>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
            <div v-if="topheaderStatus.remindShowList" class="topHeaderPop remind">
              提醒框
            </div>
          </div>
          <div v-if="!loginStatus">
            <ul class="loginBefore" >
              <li><a id="logInBtn" href="javascript:;" @click="login()">登陆</a></li>
              <li><a id="signInBtn" href="#">加入知乎</a></li>
            </ul>
          </div>

        </div>
      </div>
      <div class="minWidth clearFix" v-else key="questInfo">
        <h3 style="float:left;">{{title}}</h3>
        <div class="questionInfo">
          <ul>
            <li>
              <a href="#" class="focusQuest">关注问题</a>
            </li>
            <li>
              <a href="#" class="answert">写回答</a>
            </li>
          </ul>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  props:['title'],
  data() {
    return {
      loginStatus: false,
      userInfo: {},
      show:true,
      topheaderStatus: {
        userShowList: false,
        messageShowList: false,
        remindShowList: false
      }
    }
  },
  methods: {
    getLoginStatus() {
      this.$http.get("/api/loginStatus", {
        r: Math.random()
      }).then(data => {
        this.loginStatus = data.body.success;
        if (data.body.success === true) {
          this.userInfo = data.body.userInfo;
        }
      }, err => {
        console.log(err);
      })
    },

    login() {
      this.$http.post("/api/logIn", {
        userName: '胡钧赫',
        password: '19940601hu',
        r: Math.random()
      }).then(data => {
        if (data.body.success === true) {
          location.reload();
        } else {
          alert(data.body.message);
        }
      }, err => {
        console.log(err);
      })
    },
    logOut () {
      this.$http.get("/api/logOut?r=" + Math.random()).then(data=>{
        alert(data.body.message);
        location.reload();
      })
    },
    showList(event, obj) {
      event.cancelBubble = true;
      var _this = this.topheaderStatus;
      switch (obj) {
        case 1:
          _this.userShowList = true;
          _this.remindShowList = _this.messageShowList = false
          break;
        case 2:
          _this.messageShowList = true;
          _this.remindShowList = _this.userShowList = false
          break;
        case 3:
          _this.remindShowList = true;
          _this.userShowList = _this.messageShowList = false
          break;
      };
      document.body.addEventListener("click", pop.bind(_this), false)
    },
    Oscroll () {
      // if(location.href.indexOf("index")== -1){
      //   return false;
      // }
      var _this = this;
      window.addEventListener('mousewheel',function(e){
        if(e.deltaY > 0){
          _this.show = false;
        }else{
          _this.show = true;
        }
      })
    }
  },
  mounted() {
    this.getLoginStatus();
    this.Oscroll();
  },
  computed:{

  }
}

function pop() {
  this.userShowList = this.messageShowList = this.remindShowList = false;
  document.body.removeEventListener("click", pop, false);
}
</script>
<style lang="less">
.topNav {
    z-index:100;
    width: 100%;
    height: 53px;
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 0 1px 3px 0 rgba(0,34,77,.05);
    .minWidth {
        width: 1000px;
        margin: 0 auto;
        .askQuest,
        .logoBox,
        .modelList,
        .searchBox {
            float: left;
            height: 100%;
            line-height: 53px;
        }
        .questionInfo{
          float: right;
          height: 100%;
          line-height: 53px;
          ul{
            li{
              float:left;
              margin-right:30px;
              a{
                padding: 5px 16px;
                border-radius:5px;
                &.focusQuest{
                  background-color:#0f88eb;
                  color:#fff;
                  border:1px solid #0f88eb;
                }
                &.answert{
                  background-color:#fff;
                  color:#0f88eb;
                  border:1px solid #0f88eb;
                }
              }
            }
          }
        }
        .personInfo {
            float: right;
            height: 100%;
            line-height: 53px;
            position: relative;
            .topHeaderPop {
                position: absolute;
                top: 66px;
                background-color: #fff;
                li{
                  &:hover{
                    background-color:#f4f8fb;
                  }
                }
                &.people {
                    right: -18px;
                    width: 116px;
                    border: 1px solid #e7eaf1;
                    box-shadow: 0 5px 20px rgba(0,34,77,.1);

                }
                &.message {
                    width: 340px;
                    right: -70px;
                    padding: 10px;
                    border: 1px solid #e7eaf1;
                    box-shadow: 0 5px 20px rgba(0,34,77,.1);
                    .title {
                        text-align: center;
                        border-bottom: 1px solid #ccd8e1;
                        line-height: 48px;
                    }
                    ul {
                        li {
                            .message_one {
                                padding: 15px;
                                .left {
                                    float: left;
                                    width: 40px;
                                    height: 40px;
                                    margin-right: 10px;
                                    img {
                                        width: 100%;
                                        height: 100%;
                                        border-radius: 4px;
                                    }
                                }
                                .right {
                                    float: left;
                                    p {
                                        text-align: left;
                                        line-height: 20px;
                                        &:last-child {
                                            font-size: 14px;
                                            color: #8590a6;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                ul {
                    width: 100%;
                    li {
                        text-align: center;
                        width: 100%;
                    }
                }
            }
            ul {
                li {
                    float: left;
                    margin-right: 30px;
                }
                &.loginAfter {
                    li {
                        width: 30px;
                    }
                    .headBtn {
                        width: 30px;
                        height: 30px;
                        display: block;
                        margin-top: 7px;
                    }
                    img {
                        width: 30px;
                        height: 30px;
                    }
                }
                &.loginBefore {
                    #logInBtn {
                        color: #0d79d1;
                        background-color: #fff;
                        padding: 5px 16px;
                        border: 1px solid #0d79d1;
                        border-radius: 5px;
                    }
                    #signInBtn {
                        background-color: #0d79d1;
                        color: #fff;
                        padding: 5px 16px;
                        border: 1px solid #0d79d1;
                        border-radius: 5px;
                    }
                }
            }
        }
        .logoBox {
            padding-top: 10px;
            height: 43px;
        }
        .modelList {
            margin: 0 0 0 30px;
            ul {
                li {
                    float: left;
                    margin-right: 30px;
                    a {
                        color: #8590a6;
                        &.active {
                            color: #333;
                        }
                    }
                }
            }
        }
        .searchBox {
            border: 1px solid #e7eaf1;
            height: 33px;
            width: 326px;
            margin-top: 10px;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            input {
                border: none;
                width: 90%;
                height: 25px;
                padding: 3px 0 3px 10px;
            }
            a {}
        }
        .askQuest {
            margin-left: 30px;
            a {
                padding: 5px 14px;
                background-color: #0f88eb;
                color: #fff;
                border-radius: 5px;
            }
        }
    }
}
.fadeIn-enter-active{
  animation:fade-in 1s;
}
.fadeIn-leave-active{
  animation: fade-in .5s reverse;
}
@keyframes fade-in {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}
</style>



