//评论区
<template lang="html">
  <div class="disscusMask" v-if="showStatus.showStatus">
    <div class="disscusBox">
      <div class="close">
        <a href="javascript:;" @click='closeDissBox'>X</a>
      </div>
      <div class="title clearFix">
        <h4>{{showList}}条评论</h4>
        <a href="#">切换为时间排序</a>
      </div>
      <div class="discussList">
        <div class="discussList_box">
          <ul>
            <li v-for="(data,index) in disscusData">
              <div class="oneDiscuss">
                <p class="peopleInfo clearFix">
                  <span>
                    <img :src="data.headImg" alt=""><a href="#">{{data.userName}}</a>
                    <template v-if="data.replyCode !== 0">
                      回复 <a href="#">{{data.replyName}}</a>
                    </template>
                  </span>
                  <span>{{data.createTime}}</span>
                </p>
                <p class="content">{{data.disscus}}</p>
                <p class="actionP"><span><a href="javascript:;" @click="likeDisscus(index)">赞{{(data.likeCount)?data.likeCount:0}}</a></span> <span class="btnHover"><a href="javascript:;" @click="reply(index)">回复</a><a href="#">举报</a></span></p>
              </div>
            </li>
          </ul>
        </div>
        <div class="writeArea">
          <div class="writeAreaCon">
            <textarea name="name" v-model="sendData.disscus" :placeholder="sendData.placeholder"></textarea>
            <a href="javascript:;" class="submitDis" @click="sendDisscus">评论</a>
          </div>

        </div>
      </div>
    </div>
    <p>{{}}</p>
  </div>
</template>

<script>
export default {
  props: ['showStatus'],
  data() {
    return {
      disscusData: [],
      showDiss: this.showStatus.showStatus,
      sendData: {
        disscus: '',
        replyCode: Number,
        replyName: String,
        placeholder: '写下你的评论'
      },
      childData:{
        status:false,
        dissCount:Number
      }
    }
  },
  methods: {
    closeDissBox() {
      this.$emit("childData", this.childData);
    },
    likeDisscus(index) {
      this.$http.get('/api/likeThisDisscus?disscusCode=' + this.disscusData[index].disscusCode + '&r=' + Math.random()).then(data => {
        if (data.body.success) {
          if (data.body.message === '点赞') {
            this.disscusData[index].likeCount += 1;
            console.log(1);
          } else {
            this.disscusData[index].likeCount -= 1;
            console.log(2);
          }
        }
      })
    },
    sendDisscus() {
      if (this.sendData.disscus.replace(/(^\s*)|(\s*$)/g, "").length === 0) {
        alert("未填写内容");
        return false;
      }
      this.$http.post('/api/sendDiscuss', {
        questCode: parseInt(this.showStatus.codeNum),
        disscus: this.sendData.disscus,
        replyCode: this.sendData.replyCode,
        replyName: this.sendData.replyName
      }).then(data => {
        if (data.body.success) {
          alert("评论成功");
          this.disscusData = data.body.data;
          this.sendData.disscus = '';
          this.sendData.replyCode = 0;
          this.sendData.replyName = '';
        } else {
          alert(data.body.message);
        }
      })
    },
    reply(i) {
      this.sendData.replyCode = this.disscusData[i].userCode;
      this.sendData.replyName = this.disscusData[i].userName;
      this.sendData.placeholder = '回复' + this.disscusData[i].userName + ':';
    },
    getDissList() {
      this.$http.get('/api/getQuestDiscuss?code=' + parseInt(this.showStatus.codeNum) + '&r=' + Math.random()).then(data => {
        if (data.body.success) {
          this.disscusData = data.body.data;
          return this.DissList;
        }
      })
    }

  },
  computed:{
    showList () {
      if(this.showStatus.showStatus){
        this.getDissList();
        this.childData.dissCount = this.disscusData.length;
      }
      return this.disscusData.length;
    }
  }
}
</script>

<style lang="less">
.disscusMask {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: rgba(0,0,0,.5);
    .disscusBox {
        width: 688px;
        height: 60%;
        margin: 50px auto 0;
        background-color: #fff;
        padding: 0 15px;
        position: relative;
        .title {
            padding: 10px;
            border-bottom: 1px solid #f0f2f7;
            h4 {
                margin: 0;
                float: left;
            }
            a {
                float: right;
            }
        }
        .close {
            position: absolute;
            right: -50px;
            top: 0;
            a {
                color: #fff;
            }
        }
        .discussList {
            padding-top: 20px;
            width: 100%;
            height: 60%;
            .discussList_box {
                height: 100%;
                overflow-y: auto;
                ul {
                    width: 98%;
                    li {
                        padding: 10px 0;
                        border-bottom: 1px solid #f0f2f7;
                        &:last-child {
                            border: none;
                        }
                        .oneDiscuss {
                            .peopleInfo {
                                span {
                                    &:first-child {
                                        float: left;
                                    }
                                    &:last-child {
                                        float: right;
                                    }
                                    a {
                                        line-height: 24px;
                                    }
                                }
                                img {
                                    width: 24px;
                                    height: 24px;
                                    margin-right: 10px;
                                    vertical-align: middle;
                                }
                            }
                            .content {
                                margin: 8px 0;
                            }
                            .actionP {
                                > span {
                                    margin-right: 20px;
                                    a {
                                        color: #8590a6;
                                    }
                                    &.btnHover {
                                        opacity: 0;
                                        transition: opacity 0.5s;
                                        &:hover {
                                            opacity: 1;
                                        }
                                    }
                                    a {
                                        margin-right: 20px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .writeArea {
                width: 100%;
                height: 30%;
                .writeAreaCon {
                    position: relative;
                    margin-top: 20px;
                    textarea {
                        position: absolute;
                        width: 100%;
                        min-height: 40px;
                        z-index: 11;
                        resize: none;
                        transition: width 0.5s;
                        &:focus {
                            width: 80%;
                        }

                    }
                    .submitDis {
                        position: absolute;
                        right: 20px;
                        z-index: 10;
                        top: 50%;
                        padding: 10px;
                        color: #fff;
                        background-color: #0f88eb;
                    }

                }
            }
        }
    }
}
</style>



