<template>
<div class="editorBox">
  <div class="authorInfo clearFix">
    <div class="answerEditor-leftPart clearFix">
      <div class="headPart">
        <img :src="authorInfo.headImg" alt="">
        <div class="info">
          <p>{{authorInfo.userName}}</p>
          <p><span>86</span><a class="editExp">编辑话题经验</a></p>
        </div>
      </div>

    </div>
    <div class="answerEditor-rightPart">
      <a href="">使用匿名身份回答</a>
    </div>
  </div>
  <div class="quill-editor">
    <slot name="toolbar"></slot>
    <div ref="editor"></div>
  </div>
  <div class="subBox">
    <p><a href="#" class="options">设置</a><a href="javascript:;" class="subAns" @click="subAnswer">提交回答</a></p>
  </div>
</div>
</template>

<script>
require('quill/dist/quill.snow.css')
require('quill/dist/quill.bubble.css')
require('quill/dist/quill.core.css')
if (!window.Quill) {
  window.Quill = require('quill/dist/quill.js')
}
export default {
  name: 'quill-editor',
  data: function() {
    return {
      _content: '',
      defaultModules: {
        toolbar: [
          ['bold', 'italic', 'blockquote', 'code-block', {
            'header': 1
          }, {
            'list': 'ordered'
          }, {
            'list': 'bullet'
          }, {
            'direction': 'rtl'
          }],
          [{
            'align': []
          }],
          ['clean'],
          ['link', 'image', 'video']
        ]
      },
      loginStatus:false,
      authorInfo:{}
    }
  },
  props: {
    content: String,
    value: String,
    disabled: Boolean,
    options: {
      type: Object,
      required: false,
      default: function() {
        return {}
      }
    }
  },
  mounted: function() {
    this.initialize();
    this.loginStatusFn();
  },
  beforeDestroy: function() {
    this.quill = null
  },
  methods: {
    initialize: function() {
      if (this.$el) {

        // options and instance
        var self = this
        self.options.theme = self.options.theme || 'snow'
        self.options.boundary = self.options.boundary || document.body
        self.options.modules = self.options.modules || self.defaultModules
        self.options.modules.toolbar = self.options.modules.toolbar !== undefined ?
          self.options.modules.toolbar :
          self.defaultModules.toolbar
        self.options.placeholder = self.options.placeholder || 'Insert text here ...'
        self.options.readOnly = self.options.readOnly !== undefined ? self.options.readOnly : false
        self.quill = new Quill(self.$refs.editor, self.options)

        // set editor content
        if (self.value || self.content) {
          self.quill.pasteHTML(self.value || self.content)
        }

        // mark model as touched if editor lost focus
        self.quill.on('selection-change', (range) => {
          if (!range) {
            self.$emit('blur', self.quill)
          } else {
            self.$emit('focus', self.quill)
          }
        })

        // update model if text changes
        self.quill.on('text-change', (delta, oldDelta, source) => {
          var html = self.$refs.editor.children[0].innerHTML
          var text = self.quill.getText()
          if (html === '<p><br></p>') html = ''
          self._content = html
          self.$emit('input', self._content)
          self.$emit('change', {
            editor: self.quill,
            html: html,
            text: text
          })
        })

        // disabled
        if (this.disabled) {
          this.quill.enable(false)
        }

        // emit ready
        self.$emit('ready', self.quill)
      }
    },
    loginStatusFn (callBack) {
      this.$http.get("/api/loginStatus?r=" + Math.random()).then(data=>{
        if (data.body.success === true) {
          this.loginStatus = true;
          this.authorInfo = data.body.userInfo;
          callBack && callBack();
        }else{
          this.loginStatus = false;
        }
      })
    },
    subAnswer () {
      let _this = this;
      this.loginStatusFn(function(){
        _this.$http.post('/api/sendAnswer',{
          questCode : parseInt(_this.$route.params.id),
          r:Math.random(),
          content:_this._content
        }).then(data=>{
          alert(data.body.message);
        });
      })
    }

  },
  computed:{
    authorStatus () {
      if(this.loginStatus === true){
        return this.authorInfo
      }else{
        return false;
      }
    }
  },
  watch: {
    content: function(newVal, oldVal) {
      if (this.quill) {
        if (!!newVal && newVal !== this._content) {
          this._content = newVal
          this.quill.pasteHTML(newVal)
        } else if (!newVal) {
          this.quill.setText('')
        }
      }
    },
    value: function(newVal, oldVal) {
      if (this.quill) {
        if (!!newVal && newVal !== this._content) {
          this._content = newVal
          this.quill.pasteHTML(newVal)
        } else if (!newVal) {
          this.quill.setText('')
        }
      }
    },
    disabled: function(newVal, oldVal) {
      if (this.quill) {
        this.quill.enable(!newVal)
      }
    }
  }
}
</script>

<style lang="less">
.quill-editor img {
    max-width: 100%;
}
.editorBox {
    padding-bottom:10px;
    background-color: #fff;
    .authorInfo {
        padding: 16px 20px;
        .answerEditor-leftPart {
            float: left;
            .headPart {
                float: left;
                img {
                    float: left;
                    width: 38px;
                    height: 38px;
                    display: block;
                    margin-right: 10px;
                    vertical-align: middle;
                }
                .info {
                    float: left;
                    .editExp{
                      color:#8590a6;
                      margin-left:10px;
                    }
                }
            }

        }
        .answerEditor-rightPart {
            text-align: right;
        }
    }
    .quill-editor {
      .ql-container.ql-snow{
        border:none;
      }
        }
    .subBox{
      text-align:right;
      p{
        line-height:2.5;
      }
      .options{
        color:#8590a6;
        margin-right:20px;
      }
      .subAns{
        padding: 5px 16px;
        background-color:#0f88eb;
        color:#fff;
        margin-right:20px;
        border-radius:3px;
      }
    }
}
</style>



