<template>
  <div id="app">
    <topHearder :title="title"></topHearder>
    <questionInfo @title="showTitle" @showAnsBox="showAnsStatus"></questionInfo>
    <div class="mainBody">
      <div class="leftPart">
        <!-- <answerEditor></answerEditor> -->
        <component v-bind:is="showEditor"></component>
        <answerList></answerList>
      </div>
    </div>
  </div>
</template>

<script>
import topHearder from '@/components/topHeader'
import questionInfo from '@/components/questiondetail'
import answerEditor from '@/components/answerEditor'
import answerList from '@/components/answerList'
export default {
  name: 'app',
  data () {
    return {
      showEditor:'',
      title:''
    }
  },
  components:{
    topHearder,
    questionInfo,
    answerEditor,
    answerList
  },
  methods:{
    showAnsStatus (status) {
      this.showEditor = (status === true)?'answerEditor':false;
    },
    showTitle (title) {
      this.title = title;
    }
  }
}
</script>

<style lang="less">
html,body,ul,li,p{
  margin: 0;
  padding: 0;
  font-size: 15px;
}
body{
  background-color: #f3f3f3;
}
li{
  list-style-type: none;
}
a{
  text-decoration: none;
}

svg{
  fill:#9fadc7;
}
.clearFix{
  &:after{
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
  }
}
.mainBody{
  width: 1000px;
  margin: 10px auto 0;
  .leftPart{
    width: 694px;
  }
}
</style>



